(function(angular, moment){
    'use strict';

angular
    .module('am.date-picker', ['ngMaterial', 'ngAnimate'])
    .directive('amDatePicker', amDatePickerDirective)
    .provider('amDatePickerConfig', amDatePickerConfigProvider)
    .controller('amDatePickerDialog', DialogController);

DialogController.$inject = ["$mdDialog", "$timeout", "amDatePickerConfig"];
function DialogController($mdDialog, $timeout, amDatePickerConfig) {
    var dialog = this;
    
    dialog.cancel = cancel;
    dialog.hide = hide;
    dialog.displayYearSelection = displayYearSelection;
    dialog.hideYearSelection = hideYearSelection;
    dialog.previousMonth = previousMonth;
    dialog.nextMonth = nextMonth;
    dialog.select = select;
    dialog.selectYear = selectYear;
    dialog.today = today;

    dialog.isTodayDisabled = false;
    dialog.yearSelection = false;
    dialog.change = true;

    var delay = 110;

    init();


    function init() {
        var options = ['backButtonText', 'cancelButton', 'inputLabel', 'maxYear', 'minYear', 'locale',
            'popupDateFormat', 'todayButton', 'prevIcon', 'nextIcon'];

        for (var i = 0; i < options.length; i++) {
            if (amDatePickerConfig.hasOwnProperty(options[i])) {
                if (!angular.isDefined(dialog[options[i]]))
                    dialog[options[i]] = amDatePickerConfig[options[i]];
            }
        }

        dialog.moment = moment(dialog.Date || new Date());
        dialog.moment.locale(dialog.locale);
        dialog.monthYear = moment(dialog.Date || new Date());
    
        dialog.days = [];
        dialog.daysOfWeek = [
            moment.weekdaysMin(1),
            moment.weekdaysMin(2),
            moment.weekdaysMin(3),
            moment.weekdaysMin(4),
            moment.weekdaysMin(5),
            moment.weekdaysMin(6),
            moment.weekdaysMin(0)
        ];
        dialog.years = [];

        var minYear = (dialog.minYear || 1920),
            maxYear = dialog.maxYear || 2020;


        if (dialog.minDate) {
            minYear = Math.max(minYear, moment(dialog.minDate).year());
        }
        if (dialog.maxDate) {
            maxYear = Math.min(maxYear, moment(dialog.maxDate).year());
        }

        for (var y = minYear; y <= maxYear; y++) {
            dialog.years.push(y);
        }

        generateCalendar(!!dialog.Date);
    }

    function cancel() {
        $mdDialog.cancel();
    }

    function hide() {
        $mdDialog.hide(dialog.moment.toDate());
    }

    function displayYearSelection() {
        dialog.yearSelection = true;
        $timeout(function () {
            var yearSelector = angular.element(document.querySelector('.am-date-picker__year-selector')),
                activeYear = angular.element(document.querySelector('.am-date-picker__year--is-active')),
                activeYearHeight = activeYear[0].getElementsByTagName('p')[0].offsetHeight;

            yearSelector[0].scrollTop = activeYear[0].offsetTop - yearSelector[0].offsetTop -
            yearSelector[0].clientHeight / 2 + activeYearHeight / 2;
        });
    }

    function hideYearSelection() {
        dialog.yearSelection = false;
    }

    function nextMonth() {
        if (dialog.monthYear.isBefore(dialog.minDate, "month")) {
            dialog.monthYear.add(1, 'month');
            generateCalendar();
            onNextMonth();
        }
    }


    function onNextMonth() {
        dialog.change = false;
        dialog.next = true;
        $timeout(function () {
            dialog.change = true;
        }, delay);
    }

    function onPrevMonth() {
        dialog.next = false;
        dialog.change = false;
        $timeout(function () {
            dialog.change = true;
        }, delay);
    }

    function previousMonth() {
        if (dialog.monthYear.isAfter(dialog.maxDate, "month")) {
            dialog.monthYear.subtract(1, 'month');
            generateCalendar();
            onPrevMonth();
        }
    }

    function today() {
        var today = new Date();
        dialog.moment = moment(today);
        dialog.ngModelMoment = moment(today);
        if (dialog.yearSelection) { dialog.hideYearSelection(); }

        generateCalendar();
    }

    function select(_day) {
        if (!_day.disabled) {
            dialog.moment = _day;
            dialog.monthYear = angular.copy(_day);

            generateCalendar();
        }
    }

    function selectYear(_year) {
        dialog.hideYearSelection();
        dialog.monthYear.year(_year);

        generateCalendar();
    }

    function generateCalendar(selectableMoment) {
        dialog.days = [];
        dialog.emptyFirstDays = [];
        var previousDay = angular.copy(dialog.monthYear).date(0),
            firstDayOfMonth = angular.copy(dialog.monthYear).date(1),
            lastDayOfMonth = angular.copy(firstDayOfMonth).endOf('month'),
            maxDays = angular.copy(lastDayOfMonth).date();

        for (var i = firstDayOfMonth.day() === 0 ? 6 : firstDayOfMonth.day() - 1; i > 0; i--) {
            dialog.emptyFirstDays.push({});
        }
        for (var j = 0; j < maxDays; j++) {
            var date = angular.copy(previousDay.add(1, 'days'));
            date.selected = selectableMoment !== false && date.isSame(dialog.moment, 'day');
            date.today = date.isSame(moment(), 'day');
            if (angular.isDefined(dialog.minDate) && date.toDate() < dialog.minDate) {
                date.disabled = true;
            }
            if (angular.isDefined(dialog.maxDate) && date.toDate() > dialog.maxDate) {
                date.disabled = true;
            }
            dialog.days.push(date);
        }
        dialog.isTodayDisabled = (angular.isDefined(dialog.minDate) &&
            moment().toDate() < dialog.minDate ||
            angular.isDefined(dialog.maxDate) &&
            moment().toDate() > dialog.maxDate);
    }
}    

function amDatePickerDirective() {
    return {
        restrict: 'AE',
        templateUrl: 'am-date-picker.html',
        scope:
        {
            Date: "=ngModel",
            allowClear: '=?amAllowClear',
            cancelButton: '@?amCancelButton',
            inputDateFormat: '@?amInputDateFormat',
            inputLabel: '@?amInputLabel',
            maxDate: '=?amMaxDate',
            minDate: '=?amMinDate',
            maxYear: '=?amMaxYear',
            minYear: '=?amMinYear',
            popupDateFormat: '@?amPopupDateFormat',
            showInputIcon: '=?amShowInputIcon',
            todayButton: '@?amTodayButton'
        },
        controller: AmDatePickerController,
        controllerAs: 'amDatePicker',
        bindToController: true,
        replace: true
    };
}

AmDatePickerController.$inject = ['$scope', '$timeout', '$mdDialog', 'amDatePickerConfig'];

function AmDatePickerController($scope, $timeout, $mdDialog, amDatePickerConfig) {
    var amDatePicker = this;

    amDatePicker.clearDate = clearDate;
    amDatePicker.openPicker = openPicker;

    amDatePicker.isTodayDisabled = false;
    amDatePicker.yearSelection = false;
    amDatePicker.change = true;

    init();

    function init() {
        var options = ['backButtonText', 'cancelButton', 'inputLabel', 'maxYear', 'minYear',
            'popupDateFormat', 'todayButton', 'prevIcon', 'nextIcon', 'calendarIcon', 'allowClear',
            'clearIcon', 'showInputIcon', 'inputDateFormat', 'inputLabel', 'locale', 'minDate', 'maxDate'];

        for (var i = 0; i < options.length; i++) {
            if (amDatePickerConfig.hasOwnProperty(options[i])) {
                if (!angular.isDefined(amDatePicker[options[i]]))
                    amDatePicker[options[i]] = amDatePickerConfig[options[i]];
            }
        }
        formatViewValue();
    }

    function formatViewValue() {
        amDatePicker.value = amDatePicker.Date ? moment(amDatePicker.Date).format(amDatePicker.inputDateFormat) : undefined;
    }

    function clearDate() {
        amDatePicker.Date = undefined;
        formatViewValue();
    }

    function openPicker(ev) {
        $mdDialog.show({
            clickOutsideToClose: true,
            controller: DialogController,
            controllerAs: 'dialog',
            bindToController: true,
            parent: angular.element(document.body),
            targetEvent: ev,
            templateUrl: 'am-date-picker_content.tmpl.html',
            locals: {
                Date: amDatePicker.Date,
                minDate: amDatePicker.minDate,
                maxDate: amDatePicker.maxDate,
                todayButton: amDatePicker.todayButton,
                cancelButton: amDatePicker.cancelButton,
                backButtonText: amDatePicker.backButtonText
            }
        }).then(function (selectedDate) {
            amDatePicker.Date = selectedDate;
            formatViewValue();
        });
    }
}

function amDatePickerConfigProvider() {
    var config = {};
    config.allowClear = true;
    config.backButtonText = "Back";
    // config.cancelButtonText = "Cancel";
    config.inputDateFormat = 'LL';
    config.maxYear = 2020;
    config.minYear = 1920;
    config.locale = 'en';
    config.popupDateFormat = 'ddd, MMM D';
    config.showInputIcon = false;

    /* Icons */
    config.calendarIcon = '/dist/images/icons/ic_today_24px.svg';
    config.clearIcon = '/dist/images/icons/ic_close_24px.svg';
    config.nextIcon = '/dist/images/icons/ic_chevron_right_18px.svg';
    config.prevIcon = '/dist/images/icons/ic_chevron_left_18px.svg';

    this.setIcons = function (icons) {
        for (var key in icons) {
            config[key] = icons[key];
        }
    }

    this.setOptions = function (options) {
        for (var key in options) {
            config[key] = options[key];
        }
    }

    var props = ['allowClear', 'backButtonText', 'cancelButton', 'inputDateFormat', 'inputLabel', 
    'maxYear', 'maxDate', 'minDate', 'minYear', 'locale', 'popupDateFormat', 'showInputIcon', 'todayButton', 'calendarIcon',
    'clearIcon', 'nextIcon', 'prevIcon'];
    
    props.forEach(function(prop) {
        var name = prop.charAt(0).toUpperCase() + prop.substr(1);
        this['set' + name] = function(value){
            config[prop] = value;
            return this;
        };
    }, this);

    this.$get = function ($mdDialog, $q) {
        return new AmDatePickerService(config, $mdDialog, $q);
    };
    this.$get.$inject = ["$mdDialog", "$q"];
}
function AmDatePickerService(config, $mdDialog, $q) {
    var properties = ['allowClear', 'backButtonText', 'cancelButton', 'showInputIcon',
        'inputLabel', 'maxDate', 'minDate', 'maxYear', 'minYear', 'locale',
        'inputDateFormat', 'popupDateFormat', 'todayButton', 'calendarIcon',
        'prevIcon', 'nextIcon', 'clearIcon'];

    for (var property in properties) {
        createProperty(this, properties[property], config[properties[property]]);
    }

    function createProperty(obj, name, value) {
        Object.defineProperty(obj, name, {
            __proto__: null,
            get: function () {
                return value;
            }
        });
    }
}

})(window.angular, window.moment);